.button {
  background: transparent;
  border: none;
  padding: 0 3.5em;
  background: var(--color-blue);
  color: var(--color-white);
  height: 2.81em;
  border-radius: 7px;
  font-weight: 300;
  cursor: pointer;
  box-shadow: 0 4px 14px 0 rgba(0,118,255,0.39);
  transition: 0.3s;
  outline: none
}
.button:hover {
    opacity: 0.9;
  }
.button:active {
    background: var(--color-blue-2);
  }
