.footer {
  background: var(--color-black);
  width: 100%;
  padding: 64px 0;
}

.copyright {
  font-size: 0.75rem;
  color: var(--color-grey-2);
  margin-bottom: 0;
}

.icon {
  fill: var(--color-white);
  margin-left: 10px
}

@media (max-width: 767px) {

.icon {
    margin: 0 5px
}
  }

.row {
  display: flex;
  justify-content: space-between;
  align-items: center
}

@media (max-width: 767px) {

.row {
    flex-direction: column
}
  }

.rightColumn {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

@media (max-width: 767px) {

.rightColumn {
    align-items: center;
    order: 1;
    margin-bottom: 30px
}
  }

@media (max-width: 767px) {

.leftColumn {
    text-align: center;
    order: 2
}
  }

.greetings {
  color: var(--color-white);
  color: var(--color-grey-2);
  margin-top: 0;
}

.email {
  text-decoration: underline;
  color: var(--color-grey-2);
}
