.article {
  position: relative;
  border-radius: 6px;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 2px 20px;
  cursor: pointer;
  display: block
}

.article::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    background: linear-gradient(45deg, black, transparent);
    left: 0;
    top: 0;
    z-index: 2;
  }

@media (min-width: 768px) {
    .article:first-child {
      grid-row: 1 / 3;
    }

      .article:first-child .articleImage {
        height: 550px;
        width: 100%;
      }
  }

.article:hover .articleReadMore {
      padding-left: 10px;
    }

.article:hover .articleImage {
      transform: scale(1);
    }

.grid {
  display: grid;
  grid-template-rows: 250px 250px 250px;
  grid-gap: 38px;
  margin-bottom: 50px
}

@media (min-width: 768px) {

.grid {
    grid-template-rows: 250px 250px;
    grid-template-columns: 1fr 1fr
}
  }

.articleImage {
  height: 250px;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  position: relative;
  z-index: 1;
  transform: scale(1.2);
  transition: 2s;
}

.articleContent {
  position: absolute;
  z-index: 3;
  width: 100%;
  bottom: 0;
  color: var(--color-white);
  padding: 15px
}

@media (min-width: 768px) {

.articleContent {
    padding: 30px
}
  }

.articleTitle {
  margin-bottom: 20px;
}

.articleReadMore {
  color: var(--color-white);
  display: inline-block;
  border-radius: 3px;
  font-size: 14px;
  transition: 0.3s;
}

.articleExcerpt {
  margin-bottom: 0;
}

.title {
  margin: 50px 0 30px 0;
}
