.header {
  background: var(--color-black);
  width: 100%;
  position: relative;
  font-size: 1rem;
}

.headerInner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 4em;
}

.nav {
  position: absolute;
  top: 4em;
  left: 0;
  display: flex;
  flex-direction: column;
  background: var(--color-black);
  width: 100%;
  height: 0;
  overflow: hidden;
  transition: 0.8s
}

@media (min-width: 768px) {

.nav {
    height: 4em;
    position: static;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    background: transparent
}
  }

.navOpen {
  height: auto;
}

.navItem {
  padding: 1.2em;
  text-decoration: none;
  color: var(--color-grey-2);
  transition: 0.3s
}

.navItem:hover {
    color: var(--color-white);
  }

.navToggler {
  display: flex;
  flex-direction: column;
  border: 0;
  padding: 0;
  width: 20px;
  background: transparent;
  align-items: flex-end;
  cursor: pointer
}

.navToggler:focus {
    outline: none;
  }

.navToggler span {
    background: var(--color-white);
    height: 2px;
    margin: 2px 0;
    width: 20px;
  }

.navToggler span:nth-child(2) {
    width: 15px;
  }

@media (min-width: 768px) {

.navToggler {
    display: none
}
  }

.navItemCurrent {
  font-weight: 500;
  color: var(--color-white);
}
